import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule }    from '@angular/forms';

import { MaterialModule } from '../material/material.module';

import { AuthlessPageGuard } from './../middlewares/authless-page.guard';

import { AuthRoutingModule } from './auth-routing.module';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { ForgotpasswordComponent } from './forgotpassword/forgotpassword.component';
import { ResetpasswordComponent } from './resetpassword/resetpassword.component';
import { AuthService } from './auth.service';
import { ConfigService } from './../config/config.service';
import { FacebookLoginComponent } from './social/facebook-login/facebook-login.component';

import { FacebookModule } from 'ngx-facebook';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    AuthRoutingModule,
    MaterialModule,
    FacebookModule.forRoot()
  ],
  declarations: [LoginComponent, RegisterComponent, ForgotpasswordComponent, ResetpasswordComponent, FacebookLoginComponent],
  providers: [AuthService, ConfigService, AuthlessPageGuard]
})
export class AuthModule { }



