import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { AuthService } from './../auth/auth.service';
import { ConfigService } from './../config/config.service';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';


@Injectable()
export class HomeService {

  constructor(private authService: AuthService, private http: HttpClient, private config: ConfigService) { }

/******************************************************
 * Home feed function
 * User need to be authenticated
 ********************************************************
 */
  getFeed(): Observable<any> {

    let url: any = this.config.Api()+'/post/all';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  loadMore(data: any): Observable<any> {
    let url: any = this.config.Api()+'/post/all';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{skip:data},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

 /******************************************************
 * Get a single feed
 *  Public post view
 ********************************************************
 */
  getAPost(postId: string): Observable<any> {

    let url: any = this.config.Api()+'/post/single/' + postId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

/******************************************************
 * Home feed like function
 * User need to be authenticated
 * @param: feedId 
 * @method:  POST
 ********************************************************
 */
  like(feedId: string): Observable<any> {
    interface DataResponse {
        like: boolean   
      };
    let url: any = this.config.Api()+'/post/like';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post<DataResponse>(url,{feedId: feedId},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }
/******************************************************
 * Home feed dislike function
 * User need to be authenticated
 * @param: feedId 
 * @method: DELETE
 ********************************************************
 */
  removeLike(feedId: string): Observable<any> {
    interface DataResponse {
        like: boolean   
      };

    let url: any = this.config.Api()+'/post/like?id=' + feedId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.delete<DataResponse>(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }


  /******************************************************
 * Home feed view function
 * User need to be authenticated
 * @param: feedId 
 * @method: PUT
 ********************************************************
 */
  viewCount(feedId: string): Observable<any> {
    interface DataResponse {
        views: number,
        _id: string  
      };
    let url: any = this.config.Api()+'/post/views/' + feedId;
    let header = new HttpHeaders();
    return this.http.put<DataResponse>(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }


}



