import { Injectable , EventEmitter} from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { AuthService } from './../auth/auth.service';
import { ConfigService } from './../config/config.service';
import * as io from 'socket.io-client';

import { Observable } from 'rxjs/Observable';
import { BehaviorSubject }    from 'rxjs/BehaviorSubject';
import { Subject }    from 'rxjs/Subject';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';

@Injectable()
export class ChatService {

	public currentChats:any = [];


	// private subject = new BehaviorSubject<any>([]);


	// currentMessage = this.subject.asObservable();

// Using as chatwindows number increase
	private chatWinsource = new Subject<any>();
	chatWins = this.chatWinsource.asObservable();

// Removing chat wins
  private chatWinsourceRemove = new Subject<any>();
  chatWinsOut = this.chatWinsourceRemove.asObservable();

  // Socket 
  //private url = 'https://shielded-fjord-83188.herokuapp.com'; 
  private socket;



  constructor(
  	private authService: AuthService, 
  	private http: HttpClient, 
  	private config: ConfigService
  ) { 
    // this.socket = io(this.config.SocketUrl());

  }


  getChats(): Observable<any> {

    let url: any = this.config.Api()+'/channel/myall';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  getChatInfo(chnId:any): Observable<any> {

    let url: any = this.config.Api()+'/channel/' + chnId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

 
  // changeMessage(message: any) {
  // 	//console.log('hello', this.currentMessage);
  //     this.subject.next(message);
  // }


  addChatWin(chatId: any) {
    this.chatWinsource.next(chatId);
  }
  outChatWin(chatId: any) {
    this.chatWinsourceRemove.next(chatId);
  }


  getMessages(chnId: any): Observable<any> {

    let url: any = this.config.Api()+'/message/' + chnId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  loadMore(chnId: string, data: any): Observable<any> {
    let url: any = this.config.Api()+'/message/' + chnId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{skip:data},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  createMessage(data: any): Observable<any> {
    let url: any = this.config.Api()+'/message/create';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  getSocketMessages(chnId:any) {
    let observable = new Observable(observer => {
      this.socket = io(this.config.SocketUrl());

      this.socket.emit('join:room', {'room_name': chnId});// Listening on the chnanel

      this.socket.on('message', (data) => {
        observer.next(data);   
      });
      return () => {
        this.socket.disconnect();
      }; 
    })    
    return observable;
  }
  
  sendSocketmsg(chnId:any, msg:any) {
    this.socket = io(this.config.SocketUrl());
    this.socket.emit('join:room', {'room_name': chnId});// Listening on the chnanel
    this.socket.emit('send:message', msg);

  }

  findUser(data: any): Observable<any> {
    let url: any = this.config.Api()+'/user/search';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{user:data},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }


  getContactChannel(user: any,name: string): Observable<any> {
    let url: any = this.config.Api()+'/channel/createcontact';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{contact:user,name: name},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }


  // Post Comment Services

  // postComment(postId:any): Observable<any> {
  //   let url: any = this.config.Api()+'/post/chat';
  //   let header = new HttpHeaders().set('token', this.authService.getToken());
  //   return this.http.post(url,{feedId:postId},{headers: header })
  //              .do(data => JSON.stringify(data))
  //              .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  // }


  getComments(chnId: any): Observable<any> {

    let url: any = this.config.Api()+'/comment/' + chnId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  loadMoreComments(chnId: string, data: any): Observable<any> {
    let url: any = this.config.Api()+'/comment/' + chnId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{skip:data},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  createComment(data: any): Observable<any> {
    let url: any = this.config.Api()+'/comment/create';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  
}



