import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule }    from '@angular/forms';
import { MaterialModule } from '../material/material.module';
import { ChatBoxComponent } from './chat-box/chat-box.component';
import { ChatService } from './chat.service';
import { ChatWindowComponent } from './chat-window/chat-window.component';
import { ChatIndexComponent } from './chat-index/chat-index.component';
import { ChatCommentsComponent } from './chat-comments/chat-comments.component';
import { ChatCommentService } from './chat-comments/chat-comment.service';


@NgModule({
  imports: [
    CommonModule,
    MaterialModule,
    FormsModule
  ],
  exports: [CommonModule,ChatBoxComponent, ChatWindowComponent, ChatIndexComponent,ChatCommentsComponent],
  declarations: [ChatBoxComponent, ChatWindowComponent, ChatIndexComponent, ChatCommentsComponent],
  providers: [ChatService, ChatCommentService]
})
export class ChatModule { }



