import { Component, OnInit, ViewChild, ElementRef, Inject } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { FacebookService, InitParams , LoginResponse, LoginOptions } from 'ngx-facebook';
import { AmazingTimePickerService } from 'amazing-time-picker';

import {PostService} from './post.service';
import { ProfileService } from './../profile/profile.service';
import { ConfigService } from './../config/config.service';
import { PostClass } from './post-class';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css'],
  providers: [PostClass]
})
export class PostComponent implements OnInit {
	model: any = {};
	me: any = {};
  isProgress: boolean = false;
  message: string;
  showlinkForm: boolean = false;


 @ViewChild('fileInput') fileInput: ElementRef;
 @ViewChild('videoInput') videoInput: ElementRef;
 @ViewChild('youtTubeView') youtTubeView: ElementRef;


  constructor(
    private postService: PostService, 
    private profile: ProfileService,
    public config: ConfigService,
    private postClass: PostClass,
    public dialog: MatDialog,
    private fb: FacebookService,
    private atp: AmazingTimePickerService
  ) { 

    let initParams: InitParams = {
      appId: '443460315998361',
      xfbml: true,
      version: 'v2.8'
    };
 
    fb.init(initParams);


  }
 
  ngOnInit() {
    this.message = '';
    this.showlinkForm = false;
    this.youtTubeView.nativeElement.style.display = 'none';

    this.model.images =[];
    this.model.videos =[];
    this.model.videoLinks =[];
    this.model.published_date = new Date();
    // default value
    this.model.time =  new Date().getHours() + ":"+ new Date().getMinutes();

    this.model.date = new Date();

  	this.profile.me().subscribe(
  	data=>{
  	this.me = data;
  	},
  	error=> {
  	console.log(error);
  	});
  }

  browseImage() {
    this.fileInput.nativeElement.click();
  }
  browseVideo() {
    this.videoInput.nativeElement.click();
  }

  upload() {
    this.isProgress = true;
    
    let fileBrowser = this.fileInput.nativeElement;
    if (fileBrowser.files && fileBrowser.files[0]) {
      //console.log(fileBrowser.files)
      if (fileBrowser.files[0].size>1500000) {
        //alert('Your image is too large')
        this.message = 'Your image is too large than 1.5MB!';
        setTimeout(()=>{
          this.message = '';
          fileBrowser.value = '';
        },5000);
        this.isProgress = false;
      } else {
        const formData = new FormData();
        formData.append("image", fileBrowser.files[0]);
        this.postService.upload(formData).subscribe(res => {
          // do stuff w/my uploaded file
          //console.log(res)
          res.map((img)=>{
            this.model.images.push(img.Key);
            this.isProgress = false;
          });
        });
      }
      
    }
  }

  uploadVideo() {
    this.isProgress = true;
    
    let fileBrowser = this.videoInput.nativeElement;
    if (fileBrowser.files && fileBrowser.files[0]) {
        if (fileBrowser.files[0].size>20000000) {
          //alert('Your file is too large')
          this.message = 'Your file is too large than 20MB!';
          setTimeout(()=>{
            this.message = '';
            fileBrowser.value = '';
          },5000);
          this.isProgress = false;
        } else {
          const formData = new FormData();
          formData.append("video", fileBrowser.files[0]);
          this.postService.uploadVideo(formData).subscribe(res => {
            // do stuff w/my uploaded file
            //console.log(res)
            res.map((vid)=>{
              this.model.videos.push({file:vid.Key});
              this.isProgress = false;
            });
          });
        }  
    }
  }

  shareFb() {
    const options: LoginOptions = {
      scope: 'public_profile,email,publish_actions', // add more permission: e.g public_profile,user_friends,email,pages_show_list
      return_scopes: true,
      enable_profile_selector: true
    };

    this.fb.login(options)
      .then((response: LoginResponse)=>{
        // console.log(response)
        this.model.published_date = this.makePublishDate(this.model.date, this.model.time);
        this.postService.fbLongToken(response.authResponse).subscribe(res=>{
          if (res === true) {
            Object.assign(this.model,{automate: {facebook: true}});
            //console.log(this.model);
          }
        })
      });
    
  }


  getTime() {
    const amazingTimePicker = this.atp.open();
        amazingTimePicker.afterClose().subscribe(time => {
          this.model.time = time;
        });
  }

  makePublishDate(date:any,time:any) {
    let timeParts = time.split(":");
    let min =((+timeParts[0] * 60) + (+timeParts[1]));
    let d = new Date(date);
    d.setMinutes(min)
    let k = d.toISOString();
    return k;
  }


  post(data: any) {
    // let youtubeId = this.postClass.getYoutubeId(data.desc);

    //  this.youtTubeView.nativeElement.style.display = 'block';
    //  this.youtTubeView.nativeElement.src = 'https://www.youtube.com/embed/'+ youtubeId;
    //  this.model.videoLinks.push({site:'YouTube',key: youtubeId});

  	//console.log(this.model);
    this.model.published_date = this.makePublishDate(this.model.date, this.model.time); // make publish date

  	this.postService.post(this.model).subscribe(
  	data=> {
  	console.log(data);
  	this.model.desc = '';
    this.model.images.length = 0;
    this.model.videos.length = 0;
    this.model.videoLinks.length = 0;
    this.youtTubeView.nativeElement.style.display = 'none';
    //Reloading the page
    //window.location.reload();
  	
  	},
  	error=> {
      console.log(error)
  	});

  }

  // Youtube Link finding 
  findVideoLink(ev) {
    let youtubeId = this.postClass.getYoutubeId(ev);
    if (youtubeId!== 'error') {
       this.youtTubeView.nativeElement.style.display = 'block';
       this.youtTubeView.nativeElement.src = 'https://www.youtube.com/embed/'+ youtubeId;
       if (this.model.videoLinks.length ==0) {
        this.model.videoLinks.push({site:'YouTube',key: youtubeId});
       }

    }

  }

   
 

}




