import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd} from '@angular/router';
import { ProfileService } from './../profile.service';
import { AuthService } from './../../auth/auth.service';
import { ChatService } from '../../chat/chat.service';

import 'rxjs/add/operator/switchMap';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {
	me: any = {};
  isFriend:any;
  isProgress: boolean = false;
  message: string;

  @ViewChild('fileInput') fileInput: ElementRef;
  

  constructor(
  	private profile: ProfileService, 
  	private route: ActivatedRoute,
    private router: Router, 
    public auth: AuthService,
    private chat: ChatService
  ) { }

  ngOnInit() {
    this.message = '';
  	//console.log('profile page')
      //Getting user info
      this.userInfo(this.route.snapshot.params.userName);
      // Checking is friendship
      this.isFriendship(this.route.snapshot.params.userName);

      this.router.events.subscribe(event =>{
        if(event instanceof NavigationEnd) {
          this.userInfo(this.route.snapshot.params.userName);
          this.isFriendship(this.route.snapshot.params.userName);
         }
      });
  }

  userInfo(userId: any) {
    this.profile.userInfo(userId).subscribe(data=>{
      this.me = data.data;
      this.me.fav = data.fav;
    },
    error=>{
      console.log(error);
    })
  }

  isFriendship(userId: any) {
    this.profile.isFriend(userId).subscribe(data=>{
      this.isFriend = data;
      //console.log(data)
    },
    error=>{

    });
  }

  makeFav(userId: string) {
    this.profile.makeFav(userId).subscribe(
    data=>{
    this.me.fav = data.fav;

    },
    error=>{

    });

  }

  removeFav(userId: string) {
    this.profile.unFav(userId).subscribe(
    data=>{
    this.me.fav = data.fav;

    },
    error=>{

    });

  }


  makeFriend(userId: string) {
    this.profile.connect(userId).subscribe(
    data=>{
    this.isFriend.status = true;

    },
    error=>{

    });

  }

  unFriend(userId: string) {
    this.profile.unFriend(userId).subscribe(
    data=>{
    this.isFriend.status = false;

    },
    error=>{

    });

  }

  //Profile pic upload option
  browseProfilePic() {
    this.fileInput.nativeElement.click();

  }

  uploadProfilePic() {
    this.isProgress = true;
    
    let fileBrowser = this.fileInput.nativeElement;
    if (fileBrowser.files && fileBrowser.files[0]) {
      //console.log(fileBrowser.files)
      if (fileBrowser.files[0].size>1000000) {
        this.message = 'Your image is too large than 1MB';
        setTimeout(()=>{
          this.message = '';
          fileBrowser.value = '';
        },5000);
        this.isProgress = false;
      } else {
        const formData = new FormData();
        formData.append("image", fileBrowser.files[0]);
        this.profile.uploadProfile(formData).subscribe(res => {
          //console.log(res)
          res.map((img)=>{
            this.me.profile_pic = img.Location;
            this.saveProfilePic(img.Location); // Saving the profile pic
            this.isProgress = false;
          });
        },
        error=>{
          console.log(error);
        });
      }
      
    }
  }

  saveProfilePic(pic: string) {
    this.profile.saveProfilePic(pic).subscribe(data=>{
      this.me.profile_pic = data.profile_pic;
    },
    error=>{
      console.log(error);
    });
  }

  startChat(userId: string,userName: string) {
     let channelName = userName + ' & '+ this.auth.getUserName();
    //console.log(userId)
    this.chat.getContactChannel(userId, channelName).subscribe(result=>{
      //console.log(result);
      this.chat.addChatWin(result._id);
    },
    error=>{

    });
  }

}



