import { Component, OnInit, Input } from '@angular/core';
import { FacebookService, UIParams, UIResponse, InitParams } from 'ngx-facebook';

@Component({
  selector: 'app-facebook-share',
  templateUrl: './facebook-share.component.html',
  styleUrls: ['./facebook-share.component.css']
})
export class FacebookShareComponent implements OnInit {

	@Input('postId') postId: string;
	@Input('image') image: string;

  constructor(private fb: FacebookService) { 
  	let initParams: InitParams = {
      appId: '443460315998361',
      xfbml: true,
      version: 'v2.8'
    };
 
    fb.init(initParams);
  }

  ngOnInit() {
  }

  share() {
  	//console.log(this.image);
    var obj = {
      method: 'share_open_graph',
      hashtag: '#ibjnfbn',
      action_type: 'og.likes',
              image: 'https://tse1.mm.bing.net/th?id=OIP.cV3wgoahwR8kxtRcMrRPCQEsDI&pid=15.1&P=0&w=232&h=156',
      action_properties: JSON.stringify({
        object:'http://hoq.chat/view/?id=' + this.postId

      })
    };

  	  let params: UIParams = {
        method: 'share',
        mobile_iframe: 'true',
		    href: 'http://hoq.chat/view/?id=' + this.postId
		  };
		 
		  this.fb.ui(obj)
		    .then((res: UIResponse) => console.log(res))
		    .catch((e: any) => console.error(e));
  }

}



