import { Component, OnInit, ElementRef, ViewChild, Input } from '@angular/core';
import {  Router } from '@angular/router';
import { ChatService } from './../chat.service';
import { AuthService } from '../../auth/auth.service';
import { ConfigService } from '../../config/config.service';
import { ChatData } from './chat-data';

@Component({
  selector: 'app-chat-window',
  templateUrl: './chat-window.component.html',
  styleUrls: ['./chat-window.component.css']
})
export class ChatWindowComponent implements OnInit {

	@ViewChild('scrollMe') private chatBoxWindow: ElementRef;
  @Input('channelId') channelId: string;

  // history: string[] = [];


    channelInfo: any = {};

    wins: any = [];

    messages: any = [];

    text: string;

    isProgress: boolean = false;



  constructor(
    private chat: ChatService, 
    public auth: AuthService,
    public config: ConfigService,
    public router: Router

  ) {
  	//this.subscription = this.chat.getMessage().subscribe(message => { this.message = message; });
  	 
  }

  ngOnInit() {
    this.getChannelInfo(this.channelId);

    this.getMessages(this.channelId);
    this.text = '';

    // Socket listing for messages
    this.chat.getSocketMessages(this.channelId).subscribe((data:ChatData)=>{
       console.log('socket',data);
       if (data.uuid !== this.auth.getUuid()) {
        this.messages.push(data);
       }
    },
    error=>{

    })

  }

  getChannelInfo(chnId:any) {
    this.chat.getChatInfo(chnId).subscribe(data=>{
      // console.log(data);
      this.channelInfo = data;
    },
    error=>{

    })
  }

  closeChat() {
    this.chat.outChatWin(this.channelId);
  }

  scrollToBottom() {
    // console.log(this.chatBoxWindow.nativeElement);
    // console.log('top',this.chatBoxWindow.nativeElement.scrollTop);
    // console.log('height',this.chatBoxWindow.nativeElement.scrollHeight)
      try {
          this.chatBoxWindow.nativeElement.scrollTop = this.chatBoxWindow.nativeElement.scrollHeight;
      } catch(err) { }                 
  }

  onScroll() {
    // console.log('scrolling...',this.chatBoxWindow.nativeElement.scrollTop);
    if (this.chatBoxWindow.nativeElement.scrollTop == 0) {
      this.loadMore(this.channelId,this.messages.length);

    }
  }

  getMessages(chnId:any) {
    this.chat.getMessages(chnId).subscribe(data=>{
      //console.log(data);
      //this.messages = data;
      for (var i=0; data.length>i; i++) {
        this.messages.unshift(data[i]);
      }
    },
    error=>{
      console.log(error);
    });
  }

  loadMore(chnId: string, skip: any) {
    this.isProgress = true;
    this.chat.loadMore(chnId,skip).subscribe(data=>{
      for (var i=0; data.length>i; i++) {
        this.messages.unshift(data[i]);
      }

      this.isProgress = false;

    },
    error=>{

    });
  }

  goProfile(id:any) {
   this.router.navigate(['/user',id]);
  }

  sendMessage(text:any) {
    let pic = this.auth.getProfilePic() || '';
    let msg = {
      'room': this.channelId,
      'user': this.auth.getUserName(),
      'uuid': this.auth.getUuid(),
      'pic' : pic,
      'text': text,
      'images': [],
      'time': new Date(),
      'readby':[{userId: this.auth.getUuid()}]
    };

    this.messages.push(msg);
    this.text = '';
    //console.log(msg)
    this.chat.sendSocketmsg(this.channelId,msg);
    this.createMessage(msg); // create message
  }

  onEnterSend(event) {
    if (event.keyCode == 13) {
      //console.log(this.text);
      if (!event.shiftKey) {
        this.sendMessage(this.text);
      }
    }

  } 

  createMessage(data:any) {
   // console.log(data);
    this.chat.createMessage(data).subscribe(data=>{
      // Make images empty
      //console.log(data)

    },
    error=>{

    });
  }

  // message tools features
  sendLove() {
    let love = '<img src="http://www.pngall.com/wp-content/uploads/2016/04/Instagram-Heart-Free-Download-PNG.png" width="100%" />';

    //this.text +=  love + '';
    this.sendMessage(love);
  }

}



