import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { AuthService }      from './../auth.service';
import {FormControl, Validators} from '@angular/forms';


const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
	
  model: any = {};
  message: string;

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.pattern(EMAIL_REGEX)
    ]);



  constructor(public authService: AuthService, public router: Router) {
  	this.message = '';
  
  }

  ngOnInit() {
  }


  register() {
    this.message = 'Registering your account ...';

    this.authService.register(this.model).subscribe(result=>{
   
      if (result.success === false) {
        this.message = result.message;
      } else {


      let storeData: any = {
          token: result.token,
          apiKey: result.user.apikey,
          username: result.user.username,
          uuid: result.user._id,
          expire: result.expire,
          pic: result.user.profile_pic
        };
        localStorage.setItem('currentUser', JSON.stringify(storeData));
        this.authService.isLoggedIn = true;
        this.message = 'Registration success!';
        setTimeout(()=>{
          if (this.authService.isLoggedIn) {
            // Get the redirect URL from our auth service
            // If no redirect has been set, use the default
            let redirect = this.authService.redirectUrl ? this.authService.redirectUrl : '/';

            // Set our navigation extras object
            // that passes on our global query params and fragment
            let navigationExtras: NavigationExtras = {
              queryParamsHandling: 'preserve',
              preserveFragment: true
            };

            // Redirect the user
            this.router.navigate([redirect], navigationExtras);
          }

        },2000);
      }

    },
    error=>{
      console.log(error);
    })

  }

}



