import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd} from '@angular/router';
import { ProfileService } from './../profile.service';
import { ChatService } from '../../chat/chat.service';
import { AuthService } from './../../auth/auth.service';

@Component({
  selector: 'app-user-friends',
  templateUrl: './user-friends.component.html',
  styleUrls: ['./user-friends.component.css']
})
export class UserFriendsComponent implements OnInit {

	users:any = [];

  constructor(
  	private profile: ProfileService,
  	public router: Router,
  	private chat: ChatService,
  	public auth: AuthService
  ) { }

  ngOnInit() {
  	this.getFreinds();
  }

  getFreinds() {
  	this.profile.getConnects().subscribe(data=>{
  		this.users = data;
  	},
  	error=>{
  		console.log(error);
  	});
  }

  goProfile(id:any) {
   this.router.navigate(['/user',id]);

  }

  startChat(userId: string,userName: string) {
     let channelName = userName + ' & '+ this.auth.getUserName();
    this.chat.getContactChannel(userId, channelName).subscribe(result=>{
      this.chat.addChatWin(result._id);
    },
    error=>{

    });
  }

}



