import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { FacebookService, InitParams , LoginResponse, LoginOptions } from 'ngx-facebook';
import { AuthService }      from './../../auth.service';

@Component({
  selector: 'app-facebook-login',
  templateUrl: './facebook-login.component.html',
  styleUrls: ['./facebook-login.component.css']
})
export class FacebookLoginComponent implements OnInit {

  constructor(
    private fb: FacebookService, 
    private authService: AuthService, 
    private router: Router
  ) { 

  let initParams: InitParams = {
      appId: '443460315998361',
      xfbml: true,
      version: 'v2.8'
    };
 
    fb.init(initParams);

    }

  ngOnInit() {
  }

  fbLogin() {

  	const options: LoginOptions = {
		  scope: 'public_profile,email', // add more permission: e.g public_profile,user_friends,email,pages_show_list
		  return_scopes: true,
		  enable_profile_selector: true
		};

  	this.fb.login(options)
      .then((response: LoginResponse) => {
      	//console.log(response)
      	var promise = this.fb.api('/me?fields=id,name,email,picture.type(large),gender,education');
      	promise.then(data=>{

      		this.authService.fbLogin(data).subscribe(result=>{
      			//console.log(result);
      			let storeData: any = {
		          token: result.token,
		          apiKey: result.user.apikey,
		          username: result.user.username,
		          uuid: result.user._id,
		          expire: result.expire,
              pic: result.user.profile_pic
		        };

		        localStorage.setItem('currentUser', JSON.stringify(storeData));
		        this.authService.isLoggedIn = true;

		        //this.message = 'Login success!';

		        setTimeout(()=>{
		          if (this.authService.isLoggedIn) {
		            // Get the redirect URL from our auth service
		            // If no redirect has been set, use the default
		            let redirect = this.authService.redirectUrl ? this.authService.redirectUrl : '/';

		            // Set our navigation extras object
		            // that passes on our global query params and fragment
		            let navigationExtras: NavigationExtras = {
		              queryParamsHandling: 'preserve',
		              preserveFragment: true
		            };

		            // Redirect the user
		            this.router.navigate([redirect], navigationExtras);
		          }

		        },2000);
      		},
      		error=>{

      		});
      	});

      	promise.catch(error=>{
      		console.log(error);
      	});
      })
      .catch((error: any) => console.error(error));

  }
}



