import { Component, OnInit } from '@angular/core';
import { ChatService } from '../chat.service';

@Component({
  selector: 'app-chat-index',
  templateUrl: './chat-index.component.html',
  styleUrls: ['./chat-index.component.css']
})
export class ChatIndexComponent implements OnInit {

  wins: any = [];

  constructor(
  	private chat: ChatService
  ) { }

  ngOnInit() {
  	this.chat.chatWins.subscribe(data=>{
      //this.wins.push(data);
      if (this.wins.length>0) {
        if (this.wins.includes(data)) {
          this.wins = this.wins;
        } else {
          this.wins.unshift(data);
        }
      } else {
        this.wins.push(data);
      }
    });

    this.chat.chatWinsOut.subscribe(data=>{
      this.wins.splice(this.wins.indexOf(data),1);
    })
  }

}



