import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { PrivacyComponent } from './about/privacy.component';
import { TermsComponent } from './about/terms.component';

import { MaterialModule } from './material/material.module';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { HomeModule } from './home/home.module';
import { AuthModule } from './auth/auth.module';
import { MenuModule } from './menu/menu.module';
import { AuthGuard } from './middlewares/auth.guard';
import { PostModule } from './post/post.module';
import { ProfileModule } from './profile/profile.module';
import { NotFoundComponent } from './not-found/not-found.component';
import { SharedModule } from './shared/shared.module';
import { ChatModule } from './chat/chat.module';
import { SingleViewModule } from './single-view/single-view.module';




const appRoutes: Routes = [
  { path: '', canActivate:[AuthGuard], component: HomeComponent },
  { path: 'pages/about', component: AboutComponent },
  { path: 'pages/privacy', component: PrivacyComponent },
  { path: 'pages/terms', component: TermsComponent },
  { path: '**', component: NotFoundComponent },
];


@NgModule({
  declarations: [
    AppComponent,
    NotFoundComponent,
    AboutComponent,
    PrivacyComponent,
    TermsComponent
  ],
  imports: [
    BrowserModule,
    MaterialModule,
    BrowserAnimationsModule,
    HomeModule,
    AuthModule,
    MenuModule,
    HttpClientModule,
    RouterModule.forRoot(
      appRoutes,
      //{ enableTracing: true } // <-- debugging purposes only
    ),
    PostModule,
    ProfileModule,
    SharedModule,
    ChatModule,
    SingleViewModule
  ],
  exports: [MaterialModule, MenuModule],
  providers: [AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



