import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SuggestedUserService } from './suggested-user.service';

@Component({
  selector: 'app-suggested-user',
  templateUrl: './suggested-user.component.html',
  styleUrls: ['./suggested-user.component.css']
})
export class SuggestedUserComponent implements OnInit {

	users:any =[];

  constructor(
  	private suggested: SuggestedUserService,
  	private router: Router
  ) { }

  ngOnInit() {
  this.getUsers();
  }
  getUsers() {
  	this.suggested.getUsers().subscribe(data=>{
  		//console.log(data)
  		this.users = data;
  	},
  	error=>{
  	console.log(error)

  	})

  }

  addFriend(userId: string) {
  	this.suggested.connect(userId).subscribe(data=>{
  		//this.users.pop();
  		this.getUsers();
  	},
  	error=>{
  		console.log(error)
  	});
  }

  goProfile(id:any) {
   this.router.navigate(['/user',id]);

  }

}



