import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { AmazingTimePickerModule } from 'amazing-time-picker';

import {
	MatButtonModule, 
	MatCheckboxModule,
	MatToolbarModule,
	MatMenuModule,
	MatGridListModule,
  MatInputModule,
  MatFormFieldModule,
  MatCardModule,
  MatIconModule,
  MatProgressBarModule,
  MatTabsModule,
  MatListModule,
  MatDialogModule,
  MatTooltipModule,
  MatExpansionModule,
  MatAutocompleteModule,
  MatNativeDateModule
} from '@angular/material';

@NgModule({
  imports: [
    CommonModule,
    MatButtonModule,
    MatCheckboxModule,
    MatDatepickerModule,
    MatToolbarModule,
    MatMenuModule,
    MatGridListModule,
    MatInputModule,
    MatFormFieldModule,
    MatCardModule,
    MatIconModule,
    MatProgressBarModule,
    MatTabsModule,
    MatListModule,
    MatDialogModule,
    MatTooltipModule,
    MatExpansionModule,
    MatAutocompleteModule,
    MatNativeDateModule,
    AmazingTimePickerModule
  ],
  exports: [
  	MatButtonModule,
    MatCheckboxModule,
    MatDatepickerModule,
    MatToolbarModule,
    MatMenuModule,
    MatGridListModule,
    MatInputModule,
    MatFormFieldModule,
    MatCardModule,
    MatIconModule,
    MatProgressBarModule,
    MatTabsModule,
    MatListModule,
    MatDialogModule,
    MatTooltipModule,
    MatExpansionModule,
    MatAutocompleteModule,
    MatNativeDateModule,
    AmazingTimePickerModule
  ],
  declarations: []
})
export class MaterialModule { }



