import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { AuthService } from './../auth/auth.service';
import { ConfigService } from './../config/config.service';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';

@Injectable()
export class PostService {

  constructor(private authService: AuthService, private http: HttpClient, private config: ConfigService) { }


/******************************************************
 * Home feed create function
 * User need to be authenticated
 * @param: desc, images 
 * @method:  POST
 ********************************************************
 */
  post(data: any): Observable<any> {
    let url: any = this.config.Api()+'/post/create';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

 /******************************************************
 * Home feed upload function
 * User need to be authenticated
 * @param: desc, images 
 * @method:  POST
 ********************************************************
 */
  upload(data: any): Observable<any> {
    let url: any = this.config.Api()+'/post/uploadFeedImage';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

 /******************************************************
 * Home feed upload function
 * User need to be authenticated
 * @param: desc, images 
 * @method:  POST
 ********************************************************
 */
  uploadVideo(data: any): Observable<any> {
    let url: any = this.config.Api()+'/post/uploadFeedVideo';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }


  /******************************************************
 * Facebook logn access key getting function
 * User need to be authenticated
 * @param: accessToken 
 * @method:  PUT
 ********************************************************
 */
  fbLongToken(data: any): Observable<any> {
    let url: any = this.config.Api()+'/service/facebook/gettoken';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.put(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  downloadYoutubeVideo(YVidId: any): Observable<any> {
    let url: any = this.config.Api() + '/admin/download/' + YVidId;
    //let url: any = 'http://localhost:3000/admin/download/' + YVidId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header, responseType: "arraybuffer" })
               .do(data =>{
                console.log(data);
                return data;
               })
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

}



