import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';

import { PostService } from '../../post/post.service';

@Component({
  selector: 'app-video-player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.css'],
  providers: [PostService]
})
export class VideoPlayerComponent implements OnInit {

  @Input('youtubeId') youtubeId: string;
  @Input('width') width: string;
  @Input('height') height: string;

	@ViewChild('VideoLink') VideoLink: ElementRef;

	isPlaying: boolean = false;

  constructor(private postService: PostService) { }

  ngOnInit() {
  	this.VideoLink.nativeElement.style.display = 'none';

	  // if (this.youtubeId) {
   //    this.VideoLink.nativeElement.src = 'https://www.youtube.com/embed/'+ this.youtubeId;
   //  } else {
   //    this.VideoLink.nativeElement.style.display = 'none';
   //  }
  }

  playNow() {
  	this.isPlaying = true;
    this.VideoLink.nativeElement.style.display = 'block';
  	this.VideoLink.nativeElement.src = 'https://www.youtube.com/embed/'+ this.youtubeId + '?autoplay=1';
  }

  download(id: string) {
    console.log('downloading ...',this.postService);
    this.postService.downloadYoutubeVideo(id).subscribe(data=>{
      console.log(data);

        var file = new Blob([data], { type: 'application/binary' });
        var fileURL = URL.createObjectURL(file);
        window.open(fileURL);
        var link=document.createElement('a');
        link.href=fileURL;
        link.download= id +".mp4";
        link.click();
            //window.open(objectUrl);
    },
    error=>{
      console.log(error);
    })

  }

}



