import { NgModule,  CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';

import { HomeRoutingModule } from './home-routing.module';
import { HomeComponent } from './home.component';
import { MaterialModule } from '../material/material.module';
import { HomeService } from './home.service';
import { ConfigService } from './../config/config.service';
import { PostModule } from './../post/post.module';
import { ProfileModule } from './../profile/profile.module';
import { SuggestedUserComponent } from './suggested-user/suggested-user.component';
import { SuggestedUserService } from './suggested-user/suggested-user.service';

import { SharedModule } from '../shared/shared.module';
import { ChatModule } from '../chat/chat.module';



let schemas: any[] = [];

@NgModule({
  imports: [
    CommonModule,
    HomeRoutingModule,
    MaterialModule,
    PostModule,
    ProfileModule,
    SharedModule,
    ChatModule
  ],
  exports: [
 	HomeRoutingModule,
 	PostModule
  ],
  declarations: [HomeComponent, SuggestedUserComponent],
  providers: [HomeService,ConfigService, SuggestedUserService],
  schemas: schemas
})
export class HomeModule { }



