import { Component, OnInit } from '@angular/core';
import {  Router } from '@angular/router';
import { ChatService } from './../chat.service';
import { AuthService } from '../../auth/auth.service';

@Component({
  selector: 'app-chat-box',
  templateUrl: './chat-box.component.html',
  styleUrls: ['./chat-box.component.css']
})
export class ChatBoxComponent implements OnInit {

	channels:any = [];

  foundUsers:any = [];

  search:string = '';


	message: any = [];
  constructor(
    private chat: ChatService, 
    public auth: AuthService,
    public router: Router
  ) { 
  	
  }

  ngOnInit() {
  	//this.chat.currentMessage.subscribe(data=>this.message = data);
  	this.getChannels();
  }

  getChannels() {
  	this.chat.getChats().subscribe(data=>{
  		//console.log(data);
  		this.channels = data;
  	},
  	error=>{

  	});
  }

  startChat(chnId) {
  	console.log('making insert')
  	
  	//this.chat.changeMessage(chnId);

  	this.chat.addChatWin(chnId);

  }

  otherName(data:any) {
    return data.filter(d=>d._id !== this.auth.getUuid());
  }

  find(data:any) {
    this.chat.findUser(data).subscribe(data=>{
      //console.log(data);
      this.foundUsers = data;
    },
    error=>{
      console.log(error);
    })
    //console.log(data);
  }

  goProfile(id:any) {
   this.router.navigate(['/user',id]);
  }

}



