import { Injectable } from '@angular/core';
import { Http, Headers, Response } from '@angular/http';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/delay';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';

import { ConfigService } from './../config/config.service';

@Injectable()
export class AuthService {

   isLoggedIn = false;

  // store the URL so we can redirect after logging in
  redirectUrl: string;

  constructor(private config: ConfigService, private http: HttpClient) {
    if (localStorage.getItem('currentUser')) {
        this.isLoggedIn = true
      } else {
        this.isLoggedIn = false
      }
  }

  logout(): void {
    this.isLoggedIn = false;
    localStorage.removeItem('currentUser');
  }

  login(data: any): Observable<any> {
    let url: any = this.config.Api()+'/login';
    let header = new HttpHeaders();
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  register(data: any): Observable<any> {
    let url: any = this.config.Api()+'/register';
    let header = new HttpHeaders();
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  fbLogin(data: any): Observable<any> {
    let url: any = this.config.Api()+'/login/facebook';
    let header = new HttpHeaders();
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  getToken() {
    return JSON.parse(localStorage.getItem('currentUser')).token;
  }

  getUuid() {
    return JSON.parse(localStorage.getItem('currentUser')).uuid
  }

  getUserName() {
    return JSON.parse(localStorage.getItem('currentUser')).username
  }

  getProfilePic() {
    return JSON.parse(localStorage.getItem('currentUser')).pic
  }


}



