import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { ProfileService } from './../profile.service';
import { ConfigService } from './../../config/config.service';
import { HomeService } from './../../home/home.service';

@Component({
  selector: 'app-user-timeline',
  templateUrl: './user-timeline.component.html',
  styleUrls: ['./user-timeline.component.css']
})
export class UserTimelineComponent implements OnInit {

	feeds: any = [];
  isShowLoadMore: boolean = true;

  constructor(
  	private profile: ProfileService, 
  	private route: ActivatedRoute,
    private router: Router, 
    public config: ConfigService,
    private homeService: HomeService
  ) { }

  ngOnInit() {
    this.getFeeds(this.route.snapshot.params.userName);

    this.router.events.subscribe(event =>{
        if(event instanceof NavigationEnd) {
          this.getFeeds(this.route.snapshot.params.userName);
         }
      });
  }

  getFeeds(userName: any) {
    this.profile.getFeeds(userName).subscribe(data=>{
      //console.log(data);
      this.feeds = data;
    },
    error=>{
      console.log(error)
    });

  }


  loadMore(skip: number) {
    this.isShowLoadMore = false;
    this.profile.loadMore(skip,this.route.snapshot.params.userName).subscribe(data=>{
      //console.log(data);
      if (data.length>0) {
        for (let i=0; data.length>i; i++) {
          this.feeds.push(data[i]);
        }
        this.isShowLoadMore = true
      } else {
        this.isShowLoadMore = false;

      }
    },
    error=>{
      console.log(error)
    })
  }

  like(feedId: string, index: string) {
    //console.log(feedId,index);
    this.homeService.like(feedId).subscribe(
      data=> {
        this.feeds[index].like = data.like;
        this.feeds[index].data.likes.push(data);

      },
      error=> {

      });
  }

  removeLike(feedId: string, index: string) {
    //console.log(feedId,index);

    this.homeService.removeLike(feedId).subscribe(
      data=> {
        this.feeds[index].like = data.like;
        this.feeds[index].data.likes.pop();
      },
      error=> {

      });
  }

  goProfile(id:any) {
   this.router.navigate(['/user',id]);

  }

  goDetailViewPost(id:any) {
   this.router.navigate(['/view',id]);

  }

  viewCount(postId:any,index: number) {
    this.homeService.viewCount(postId).subscribe(
      data=>{
        this.feeds[index].data.views +=1;
      },
      error=>{
        console.log(error);
      });
  }

}



