export class PostClass {
	getYoutubeId(url: string) {
		 let regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
    let match = url.match(regExp);

    if (match && match[2].length == 11) {
        return match[2];
    } else {
        return 'error';
    }
	}

	//let checkifUrl = str.match(/(^|\s)http:(\s|$)/gi);
}



