import { Component, OnInit , ViewChild, ElementRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd} from '@angular/router';
import { ProfileService } from './profile.service';
import { AuthService } from './../auth/auth.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  me: any = {};
  isProgress: boolean = false;
  message: string;


  @ViewChild('coverFileInput') coverFileInput: ElementRef;

  constructor(
  	private profile: ProfileService,
    public auth: AuthService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
  	//Getting user info
      this.userInfo(this.route.snapshot.params.userName);
      this.router.events.subscribe(event =>{
        if(event instanceof NavigationEnd) {
          this.userInfo(this.route.snapshot.params.userName);
         }
      });
  }

  userInfo(userId: any) {
    this.profile.userInfo(userId).subscribe(data=>{
      this.me = data.data;
      this.me.fav = data.fav;
    },
    error=>{
      console.log(error);
    })
  }


  browseCoverPic() {
    this.coverFileInput.nativeElement.click();

  }
  uploadCoverPic() {
    this.isProgress = true;
    
    let fileBrowser = this.coverFileInput.nativeElement;
    if (fileBrowser.files && fileBrowser.files[0]) {
      //console.log(fileBrowser.files)
      if (fileBrowser.files[0].size>1000000) {
        this.message = 'Your image is too large than 1MB';
        setTimeout(()=>{
          this.message = '';
          fileBrowser.value = '';
        },5000);
        this.isProgress = false;
      } else {
        const formData = new FormData();
        formData.append("image", fileBrowser.files[0]);
        this.profile.uploadProfile(formData).subscribe(res => {
          //console.log(res)
          res.map((img)=>{
            this.me.profile_pic = img.Location;
            this.saveCoverPic(img.Location); // Saving the profile pic
            this.isProgress = false;
          });
        },
        error=>{
          console.log(error);
        });
      } 
    }
  }

  saveCoverPic(pic: string) {
    this.profile.saveCoverPic(pic).subscribe(data=>{
      this.me.cover_pic = data.cover_pic;
    },
    error=>{
      console.log(error);
    });
  }

}



