import { Component, OnInit, ElementRef, ViewChild, Input } from '@angular/core';
import {  Router } from '@angular/router';
import { ChatService } from './../chat.service';
import { AuthService } from '../../auth/auth.service';
import { ConfigService } from '../../config/config.service';
import { ChatData } from '../chat-window/chat-data';

@Component({
  selector: 'app-chat-comments',
  templateUrl: './chat-comments.component.html',
  styleUrls: ['./chat-comments.component.css']
})
export class ChatCommentsComponent implements OnInit {
	
	@Input('postId') postId: string;

	  channelInfo: any = {};
    messages: any = [];
    text: string;
    isProgress: boolean = false;

  constructor(
    private chat: ChatService, 
    public auth: AuthService,
    public config: ConfigService,
    public router: Router
  ) { }

  ngOnInit() {
  //console.log(this.postId);
  //this.getChannel(this.postId);
    this.channelInfo._id = this.postId;
    this.getComments(this.postId);
  	this.getSocketSms(this.postId);
  }

  // getChannel(postId: string) {
  // 	this.chat.postComment(postId).subscribe(data=>{
  // 	//console.log(data);
  // 	this.channelInfo = data;
  // 	this.getMessages(data._id);
  // 	this.getSocketSms(data._id);
  // 	},
  // 	error=>{

  // 	});
  // }


  getComments(chnId:any) {
    this.chat.getComments(chnId).subscribe(data=>{
     // console.log(data);
      //this.messages = data;
      for (var i=0; data.length>i; i++) {
        this.messages.unshift(data[i]);
      }
    },
    error=>{
      //console.log(error);
    });
  }

  // Socket listing for messages
  getSocketSms(chnId:any) {
  	this.chat.getSocketMessages(chnId).subscribe((data:ChatData)=>{
      // console.log('socket',data);
       if (data.uuid !== this.auth.getUuid()) {
        this.messages.push(data);
       }
    },
    error=>{

    })
  }

  goProfile(id:any) {
   this.router.navigate(['/user',id]);
  }

  onEnterSend(chnId,event) {
    if (event.keyCode == 13) {
     // console.log(this.text);
      if (!event.shiftKey) {
        this.sendMessage(chnId,this.text);
      }
    }

  }

  sendMessage(chnId:any,text:any) {
    let pic = this.auth.getProfilePic() || '';
    let msg = {
      'room': chnId,
      'user': this.auth.getUserName(),
      'uuid': this.auth.getUuid(),
      'pic' : pic,
      'text': text,
      'images': [],
      'time': new Date(),
      'readby':[{userId: this.auth.getUuid()}]
    };

    this.messages.push(msg);
    this.text = '';
    //console.log(msg)
    this.chat.sendSocketmsg(chnId,msg);
    this.createComment(msg); // create message
  }

  createComment(data:any) {
    this.chat.createComment(data).subscribe(data=>{
      // Make images empty
    },
    error=>{

    });
  }

}



