import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { AuthService } from './../auth/auth.service';
import { ConfigService } from './../config/config.service';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';

@Injectable()
export class ProfileService {

  constructor(
  	private authService: AuthService, 
	  private http: HttpClient, 
	  private config: ConfigService
  ) { }

  me(): Observable<any> {

    let url: any = this.config.Api()+'/user/me';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  userInfo(userId: any): Observable<any> {

    let url: any = this.config.Api()+'/user/' + userId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  getFeeds(userId: any): Observable<any> {

    let url: any = this.config.Api()+'/post/user/' + userId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  loadMore(data: any, User: string): Observable<any> {
    let url: any = this.config.Api()+'/post/user/' + User;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{skip:data},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  isFriend(userId: any): Observable<any> {

    let url: any = this.config.Api()+'/connect/check/' + userId;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  makeFav(User: any): Observable<any> {
    let url: any = this.config.Api()+'/favorite/create';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{userId:User},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  unFav(User: any): Observable<any> {
    let url: any = this.config.Api() + '/favorite/del?id=' + User;
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.delete(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  connect(User: any): Observable<any> {
    let url: any = this.config.Api()+'/connect/create';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{connectTo:User},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  unFriend(User: any): Observable<any> {
    let url: any = this.config.Api()+'/connect/cancel';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{connectTo:User},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }


  uploadProfile(data: any): Observable<any> {
    let url: any = this.config.Api()+'/user/uploadProfile';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,data,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  saveProfilePic(data: any): Observable<any> {
    let url: any = this.config.Api()+'/user/profilepic';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{pic:data},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  saveCoverPic(data: any): Observable<any> {
    let url: any = this.config.Api()+'/user/saveCoverPic';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.post(url,{pic:data},{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }

  getConnects(): Observable<any> {

    let url: any = this.config.Api()+'/connect/myall';
    let header = new HttpHeaders().set('token', this.authService.getToken());
    return this.http.get(url,{headers: header })
               .do(data => JSON.stringify(data))
               .catch((error:HttpErrorResponse)=>Observable.throw(error || 'Server error'));
  }
}



