import { Component, OnInit } from '@angular/core';
import { Router, RouterModule } from '@angular/router';
import { AuthService } from './../auth/auth.service';
import { HomeService } from './home.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ConfigService } from './../config/config.service';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers: []
})
export class HomeComponent implements OnInit {

	feeds: any = [];
  isShowLoadMore: boolean = true;

  constructor(
  	private authService: AuthService, 
  	public homeService: HomeService, 
  	private http: HttpClient,
  	public config: ConfigService,
    private router: Router
  ) { }

  ngOnInit() {
  	this.homeService.getFeed().subscribe(data=>{
  		//console.log('test', data)
      this.feeds = data;
  	},
    error=> {
      console.log(error)
    });
  }

  loadMore(skip: number) {
    this.isShowLoadMore = false;
    this.homeService.loadMore(skip).subscribe(data=>{
      //console.log(data);
      if (data.length>0) {
        for (let i=0; data.length>i; i++) {
          this.feeds.push(data[i]);
        }
        this.isShowLoadMore = true
      } else {
        this.isShowLoadMore = false;

      }
    },
    error=>{
      console.log(error)
    })
  }

  like(feedId: string, index: string) {
  	//console.log(feedId,index);
    this.homeService.like(feedId).subscribe(
      data=> {
        this.feeds[index].like = data.like;
        this.feeds[index].data.likes.push(data);

      },
      error=> {

      });
  }

  removeLike(feedId: string, index: string) {
  	//console.log(feedId,index);

    this.homeService.removeLike(feedId).subscribe(
      data=> {
        this.feeds[index].like = data.like;
        this.feeds[index].data.likes.pop();
      },
      error=> {

      });
  }

  goProfile(id:any) {
   this.router.navigate(['/user',id]);

  }

  goDetailViewPost(id:any) {
   this.router.navigate(['/view',id]);

  }

  viewCount(postId:any,index: number) {
    this.homeService.viewCount(postId).subscribe(
      data=>{
        this.feeds[index].data.views +=1;
      },
      error=>{
        console.log(error);
      });
  }
  

}



