import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MaterialModule } from '../material/material.module';
import { VideoPlayerComponent } from './video-player/video-player.component';
import { FacebookShareComponent } from './social-share/facebook-share/facebook-share.component';
import { TwitterShareComponent } from './social-share/twitter-share/twitter-share.component';

@NgModule({
  imports: [
    CommonModule,
    MaterialModule
  ],
  exports: [CommonModule,VideoPlayerComponent, FacebookShareComponent, TwitterShareComponent],
  declarations: [VideoPlayerComponent, FacebookShareComponent, TwitterShareComponent]
})
export class SharedModule { }



