import { Component, OnInit , ViewChild, ElementRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd} from '@angular/router';
import { AuthService } from './../auth/auth.service';
import { HomeService } from './../home/home.service';
import { ConfigService } from './../config/config.service';

@Component({
  selector: 'app-single-view',
  templateUrl: './single-view.component.html',
  styleUrls: ['./single-view.component.css']
})
export class SingleViewComponent implements OnInit {

  feed:any;

  constructor(
    public auth: AuthService,
    private route: ActivatedRoute,
    private router: Router,
    public homeService: HomeService,
    public config: ConfigService,
  ) { }

  ngOnInit() {
  	let postname = this.route.snapshot.params.postName;
  	//console.log(postname)

  	this.homeService.getAPost(postname).subscribe(data=>{
  		//console.log(data);
      this.feed = data;
  	},
  	error=>{
  		//console.log(error);
  	})
  }


  goProfile(id:any) {
   this.router.navigate(['/user',id]);

  }

  viewCount(postId:any) {
    this.homeService.viewCount(postId).subscribe(
      data=>{
        this.feed.data.views +=1;
      },
      error=>{
        //console.log(error);
      });
  }

  like(feedId: string) {
    this.homeService.like(feedId).subscribe(
      data=> {
        this.feed.like = data.like;
        this.feed.data.likes.push(data);
      },
      error=> {

      });
  }

  removeLike(feedId: string) {
    this.homeService.removeLike(feedId).subscribe(
      data=> {
        this.feed.like = data.like;
        this.feed.data.likes.pop();
      },
      error=> {

      });
  }

}



